import React from 'react';

const SearchBox = React.memo(({ searchChange }) => {
  return (
    <div className='pa2'>
      <input
        aria-label='Search robots'
        className='pa3 ba b--green bg-lightest-blue'
        type='search'
        placeholder='search robots'
        onChange={searchChange}
      />
    </div>
  );
})

export default SearchBox;


